.logo{
  z-index: 2;
  position: absolute;
  left: 2rem;
  top: 2rem;
  color: var(--light);
  font-size: var(--size-1);
  text-shadow: 2px 2px 5px var(--transp-50);
  background-color: var(--transp-15);
  border-radius: 1rem;
  padding: 0.75rem;
  text-align: center;
  letter-spacing: 0.15rem;
}